.form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchInput {
  width: 200px;
  border: 2px solid #eee;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  padding: 10px;
}

.resetButton {
  margin-left: 10px;
  width: 40px;
  cursor: pointer;
  background-color: red;
  height: 40px;
  border: none;
  border-radius: 50%;
}
