.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
}

.coinLogo {
  width: 50px;
  height: 50px;
}

.coinText {
  text-align: start;
  width: 220px;
  color: white;
}

.coinName {
  font-size: 16px;
  font-weight: 600;
}

.coinSymbol {
  text-transform: uppercase;
}

.incomeStyle {
  color: green;
}
.declineStyle {
  color: red;
}
